import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue'
import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
import uniDrawer from '@/components/uni-drawer/uni-drawer.vue'
export default {
	components: {
		uniSearchBar,
		uniList,
		uniListItem,
		uniDrawer
	},
	data() {
		return {
			title: '产品',
			productCategory: [],
			datas: [],
			searchDatas: [],
			searchKey: '',
			curSelectType: '00000',
			typeMenu: false
		}
	},
	onShow() {
		this.productCategory = uni.getStorageSync('productCategory').productCategories
		this.productCategory.unshift({ productcategoryid: '00000', productcategoryname: '所有分类' })
		this.datas = uni.getStorageSync('productList')
		this.searchDatas = this.datas
	},
	computed: {
		headerHeight() {
			return this.$headerIsSearchHeight
		},
		mainHeight() {
			return this.$mainIsSearchHeight
		}
	},
	methods: {
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			})
		},
		handleAdd() {
			uni.navigateTo({
				url: './add/add'
			})
		},
		handleEdit(val) {
			if (val.iseditable == 0) {
				uni.navigateTo({
					url: './edit/edit0?item=' + JSON.stringify(val)
				})
			} else {
				uni.navigateTo({
					url: './edit/edit1?item=' + JSON.stringify(val)
				})
			}
		},
		handleSelectType(val) {
			this.curSelectType = val.productcategoryid
			if (val.productcategoryid !== '00000') {
				this.searchDatas = this.datas.filter((item) => {
					return item.productcategory == val.productcategoryname
				})
			} else {
				this.searchDatas = this.datas
			}
			this.handleTypeMenuClose()
		},
		handleSearch(val) {
			if (val.value) {
				this.searchDatas = this.datas.filter((item) => {
					if (!item.productname) {
						item.contactunitname = ''
					}
					if (!item.querycode) {
						item.querycode = ''
					}
					return item.productname.indexOf(val.value) !== -1 || item.querycode.toLowerCase().indexOf(val.value.toLowerCase()) !== -1
				})
			} else {
				this.searchDatas = this.datas
			}
		},
		handleTypeMenuOpen() {
			this.typeMenu = !this.typeMenu
		},
		handleTypeMenuClose() {
			this.typeMenu = false
		}
	}
}
